section {
    padding-top: 35px;
    padding-bottom: 20px;
}


.p-main,
.footer,
header,
button,
nav {
    overflow: hidden;
}



// page.php
.page-center.container {
    margin-top: 60px;
    margin-bottom: 80px;
}

.section_header {
    margin-bottom: 50px;
    text-transform: uppercase;
    color: $color-main;
    display: inline-block;
    border-bottom: 1px solid $color-hover;
}


.section_header h2 {
    font-size: 24px;
    margin: 0;
    padding: 0 10px;

}





.header_container {
    text-align: left;


}



.header_container_main {
    text-align: center;


}




// button plus button sumbit z control form 7

.button,
.wpcf7-form-control.wpcf7-submit {
    cursor: pointer;
    padding: 10px 60px;
    text-transform: uppercase;
    text-align: center;
    text-decoration: none;
    background-color: $color-hover;
    border: 1px solid $color-hover !important;
    border-radius: 15px;
    color: $color-hover;
    font-size: 18px;
    font-weight: 700;
    letter-spacing: 0.6px;
    box-shadow: inset 0 0 0 60px $color-background;
    transition: 0.5s;

}

.button:hover,
.wpcf7-form-control.wpcf7-submit:hover {
    box-shadow: inset 0 0 0 0px $color-background;
    color: $color-background;
    outline: none;


}



// banner



.banner_text.container {
    text-align: center;
    text-transform: uppercase;
    font-size: 20px;
    font-weight: 600;
    background: linear-gradient(170deg, rgb(239, 248, 255) 0%, rgb(200, 245, 250) 33%, rgb(117, 229, 243) 53%, rgb(51, 197, 236) 73%, rgb(92, 176, 238) 100%);
    max-width: 100%;
    height: 175px;
    line-height: 150%;
    padding-top: 60px;
    margin-bottom: 80px;
    overflow: hidden;

}



.banner_text.container.single {
    line-height: 175px;
    padding-top: 0;
}


@include bp(1000px, 'max') {
    .banner_text.container {
        font-size: 18px;

    }
}

@include bp(768px, 'max') {
    .banner_text.container {
        font-size: 16px;
        padding-top: 65px;

    }
}

@include bp(660px, 'max') {
    .banner_text.container {
        font-size: 14px;


    }
}

@include bp(540px, 'max') {
    .banner_text.container {
        padding-top: 55px;

    }
}

@include bp(510px, 'max') {
    .banner_text.container.single {
        padding-top: 75px;
        line-height: 150%;
    }
}



@include bp(350px, 'max') {
    .banner_text.container {
        font-size: 13px;

    }
}

@include bp(310px, 'max') {
    .banner_text.container {
        padding-top: 40px;

    }
}


@include bp(270px, 'max') {
    .banner_text.container.single {
        padding-top: 65px;

    }
}

@include bp(258px, 'max') {
    .banner_text.container {
        padding-top: 20px;

    }


}